<template>
  <div>
    <block_header></block_header>
    <progressbar></progressbar>
    <b-container class="list">
      <b-row>
        <b-col id="imageBox">
          <img
            src="../../assets/back.jpg"
            style="height: 300px;width:100%"
            alt=""
            class="rounded-lg"
          />
        </b-col>
      </b-row>
      <b-row id="introductionBox">
        <b-col>
          {{ block_introduction }}
        </b-col>
      </b-row>
      <b-row>
        <b-col md="8">
          <router-link
            style="color:black"
            class="link"
            :to="page.url"
            v-for="(page, index) in pages"
            :key="index"
          >
            <pageCard :page="page"></pageCard>
          </router-link>
        </b-col>
        <b-col md="4">
          <div id="sider" class="rounded-lg shadow">
            <h1>我要发贴</h1>
            <b-form @submit="onSubmit" @reset="onReset">
              <b-form-group label="标题：" label-for="input-1">
                <b-form-input
                  v-model="form.title"
                  id="input-1"
                  placeholder="Enter your name"
                ></b-form-input>
              </b-form-group>
              <b-form-group label="摘要：" label-for="input-1">
                <b-form-input
                  v-model="form.abstract"
                  id="input-1"
                  placeholder="Enter your name"
                ></b-form-input>
              </b-form-group>
              <b-form-group label="正文：" label-for="input-3">
                <b-form-textarea
                  id="input-3"
                  v-model="form.text"
                  placeholder="Enter something..."
                  rows="3"
                  max-rows="6"
                ></b-form-textarea>
              </b-form-group>
              <b-button type="submit" :disabled="available" variant="primary"
                >Submit</b-button
              >
              <b-button type="reset" variant="danger">Reset</b-button>
            </b-form>
          </div>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col>
          <page_nav :pageNum="count"></page_nav>
        </b-col> </b-row
      ><block_footer></block_footer>
    </b-container>
  </div>
</template>
<script>
import { getPageQueryForViewByBlock } from "../../apis/api";
import block_header from "../common/Header";
import block_footer from "../common/Footer";
import page_nav from "../common/pagecounter";
import progressbar from "../common/progress";
import pageCard from "../common/postCard";
export default {
  data() {
    return {
      total_page: 100,
      page_count: 20,
      pages: [
        {
          pid: 111,
          uid: 111,
          bid: this.$route.block_id,
          summary: "摘要",
          content: "正文",
          title: "标题",
          date: "发布时间",
          view: 10,
          liked: 10,
          isCanshow: true,
          url: ""
        },
        {
          pid: 111,
          uid: 111,
          bid: this.$route.block_id,
          summary: "摘要",
          content: "正文",
          title: "标题",
          date: "发布时间",
          view: 10,
          liked: 10,
          isCanshow: true,
          url: ""
        },
        {
          pid: 111,
          uid: 111,
          bid: this.$route.block_id,
          summary: "摘要",
          content: "正文",
          title: "标题",
          date: "发布时间",
          view: 10,
          liked: 10,
          isCanshow: true,
          url: ""
        }
      ],
      available: true,
      block_introduction: "这是板块介绍介绍介绍",
      form: {
        title: "",
        abstract: "",

        text: ""
      }
    };
  },
  components: {
    block_header,
    block_footer,
    progressbar,
    page_nav,
    pageCard
  },
  methods: {
    onSubmit(e) {
      e.preventDefault();
    },
    onReset(e) {
      e.preventDefault();
    }
  },
  mounted() {
    if (this.pages.length == 0) {
      getPageQueryForViewByBlock(
        this.$route.params.count,
        10,
        this.$route.params.block_id
      ).then(res => {
        this.pages = res.data;
      });
    }
    for (var i = 0; i < this.pages.length; i++) {
      this.pages[i].url = "/page/" + this.pages[i].pid;
    }
  },
  computed: {
    count() {
      return this.total_page / this.page_count;
    }
  }
};
</script>
<style lang="scss" scoped>
.list {
  text-align: left;
  .link {
    text-decoration: none;
  }
  #imageBox {
    width: 100%;
    height: 280px;
  }
  #introductionBox {
    color: red;
    text-align: left;
  }
}
#sider {
  padding: 5%;
  position: sticky;
  background-color: white;
  top: 100px;
}
</style>
